span{
    color: #FCDD08;
  
}
.heading{
    text-align: center;
}
.subHeading{
    font-size: 30px;
    color: #FCDD08;
}
.secondBlock{
    margin-left: 100px;
}
.logo{
    margin: 25px;
}
.bottom{
    display: flex;
    flex-direction: row;
    background-color: #FCDD08;
    width: 1497px;
    height: 159px;
   
}
.Container{
    display: flex;
    flex-direction: row;
}
li{
    list-style: none;
    
}
p{
    width: 300px;
    text-align: justify;
}

.top{
    color: red;
    text-align: center;
    font-size:xx-large;
    font-weight: bold;
    margin: 30px;
    margin-right: 150px;
    float: left;
}
.sub{
    color: black;
    font-size:xx-large;
    font-weight: bold;
    margin: auto;
    
}
@media only screen and (min-width:320px) and (max-width:480px){

    .logo{
        margin: 25px;
        display: flex;
        flex-direction: column;
    }
    .heading{
        text-align: center;
        font-size: 30px;
    }
    .subHeading{
        font-size: 28px;
    }
    .bottom{
        background-color: #FCDD08;
    } 
    .top{
        color: red;
        text-align: center;
        font-size:medium;
        font-weight: bold;
        margin-top: 15px;
        margin-bottom: 15px;
    }
    .sub{
        color: black;
        font-size:medium;
        font-weight: bold;
        margin: auto;
       
    }  
   

}
